.recruitment__section {
  position: relative;
  padding-top: 64px;
  padding-bottom: 48px;
  background-color: #d5d4d9;
  box-shadow: 0px 32px 16px rgba(0, 0, 0, 0.09), 0px 16px 8px rgba(0, 0, 0, 0.09),
    0px 8px 4px rgba(0, 0, 0, 0.09);
}

.recruitment__section-bg {
  height: 167px;
  width: 100%;
  bottom: 84px;
  background-size: contain;
  background-position: center;
  z-index: 2;
  position: absolute;
  background-image: url('../images/recruitment/steps-flow-mob.png');
}

.recruitment__title {
  font-weight: 900;
  font-size: 32px;
  line-height: 1.1;
  letter-spacing: -1px;
  color: #202523;

  margin-bottom: 28px;
}

.recruitment__text--wrapper {
  margin-bottom: 55px;
}

.recruitment__text {
  font-size: 16px;
  line-height: 1.8;
  color: #485563;

  &:not(:last-child) {
    margin-bottom: 12px;
  }
}

.slider--wrapper {
  height: 514px;
  background-color: #fff;
  background-repeat: no-repeat;
  background-position: center bottom 30px;
  //   box-shadow: 0px 32px 16px rgba(0, 0, 0, 0.09), 0px 16px 8px rgba(0, 0, 0, 0.09),
  //     0px 8px 4px rgba(0, 0, 0, 0.09), 0px 4px 2px rgba(0, 0, 0, 0.09),
  //     0px 2px 1px rgba(0, 0, 0, 0.09);
  border-radius: 16px;
  padding: 32px 25px;

  position: relative;
  margin-bottom: 124px;
}

.slider--wrapper-1 {
  background-image: url('../images/recruitment/mob.png');
}

.slider--wrapper-2 {
  background-image: url('../images/recruitment/mob2.png');
}

.slider--wrapper-3 {
  background-image: url('../images/recruitment/mob.png');
}

.slider--wrapper-4 {
  background-image: url('../images/recruitment/mob2.png');
}

.slider--label {
  color: #545460;
  font-size: 14px;
  line-height: 1.1;
  text-transform: uppercase;
  font-weight: 800;

  margin-bottom: 16px;
}

.slider--title {
  font-weight: 800;
  font-size: 32px;
  line-height: 1.1;
  letter-spacing: -1px;
  color: #202523;
  margin-bottom: 24px;

  & > span {
    display: block;
  }
}

.slider--text {
  font-size: 16px;
  line-height: 1.56;
  color: #596067;
}

.swiper-pagination-bullet {
  height: 16px;
  width: 16px;

  background-image: url('../images/recruitment/ellipse.png');
  box-shadow: inset 0px 2px 2px rgba(0, 0, 0, 0.09);
}

.swiper-pagination-bullet-active {
  width: 40px;
  height: 16px;
  background-image: none;
  border-radius: 16px;
  background-color: $accentColor;
}

.slider--text-2 {
  display: none;
}

@media screen and (min-width: 768px) {
  .recruitment__section {
    padding-top: 80px;
    padding-bottom: 56px;
  }

  .recruitment__title {
    margin-bottom: 32px;
  }

  .slider--wrapper {
    width: 354px;
  }

  .swiper-slide {
    width: auto;
  }

  .slider--title {
    & > span {
      display: inline;
    }
  }

  .recruitment__text--wrapper {
    display: flex;
  }

  .recruitment__text {
    width: 338px;
    margin-bottom: 0;

    &:not(:last-child) {
      margin-right: 52px;
      margin-bottom: 0;
    }
  }

  .recruitment__text--wrapper {
    margin-bottom: 32px;
  }

  .slider--wrapper {
    margin-bottom: 132px;
  }

  .recruitment__section-bg {
    bottom: 92px;
    background-image: url('../images/recruitment/steps-flow-tab.png');
    background-repeat: no-repeat;
    background-position: center;
  }
}

@media screen and (min-width: 1440px) {
  .recruitment__section {
    padding-top: 100px;
    padding-bottom: 86px;
  }

  .slide-3 {
    display: none;
  }
  .slide-4 {
    display: none;
  }

  .recruitment__title--wrapper {
    display: flex;
    margin-bottom: 54px;
  }

  .recruitment__text--wrapper {
    display: block;
  }

  .recruitment__title {
    margin-bottom: 0;
    margin-right: 285px;
    width: 363px;
    font-size: 62px;
  }

  .recruitment__text {
    width: 612px;
    font-size: 24px;
    line-height: 1.56;

    &:not(:last-child) {
      margin-right: 0;
      margin-bottom: 24px;
    }
  }

  .slider--wrapper {
    width: 620px;
    height: 457px;
  }

  .slider--title {
    font-style: 32px;
    margin-bottom: 16px;

    & > span {
      display: block;
    }
  }

  .slider--text {
    width: 300px;

    &:not(:last-child) {
      margin-right: 24px;
    }
  }

  .slider--text-2 {
    display: block;
    width: 248px;
  }

  .slider--text-wrapper {
    display: flex;
  }

  .slider--wrapper {
    background-position: bottom 58px right 35px;
  }

  .slider--wrapper-2 {
    background-position: bottom 58px right 35px;
    background-image: url('../images/recruitment/desc2.png');
  }

  .recruitment__section-bg {
    bottom: 118px;
    background-image: url('../images/recruitment/steps-flow-desc.png');
    background-repeat: no-repeat;
    background-position: center;
  }
}
