.hero__section {
  padding-bottom: 60px;
  background-color: $heroBgcColor;
}

.hero__first-wrapper {
  margin-bottom: 42px;
  position: relative;
}

.hero__title {
  font-size: 40px;
  line-height: 1.11;
  letter-spacing: -1px;
  font-weight: 900;
  color: $whiteColor;
  margin-bottom: 16px;
}

.hero__title-decoration {
  color: $accentColor;
}

.hero__text {
  font-size: 16px;
  line-height: 1.55;
  color: $whiteColor;
}

.hero__second-wrapper {
  margin-bottom: 85px;
}

.hero__firstImg-wrapper {
  margin-bottom: 4px;
  height: 140px;
  background-image: url('../images/hero/hero-mobile1.png');
  background-position: center;
}

@media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
  .hero__firstImg-wrapper {
    background-image: url('../images/hero/hero-mobile1@2x.png');
    background-size: contain;
  }
}

.hero__secondImg-wrapper {
  height: 70px;
  background-image: linear-gradient(180deg, rgba(2, 2, 19, 0) 0%, #020213 100%),
    url('../images/hero/hero-mobile2.png');
  background-position: center;
}

@media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
  .hero__secondImg-wrapper {
    background-image: linear-gradient(180deg, rgba(2, 2, 19, 0) 0%, #020213 100%),
      url('../images/hero/hero-mobile2@2x.png');
    background-size: contain;
  }
}

.hero__button-wrapper {
  text-align: center;
}

.hero-btn {
  width: 172px;
  height: 40px;
  background-color: $accentColor;
  color: $heroBgcColor;
  font-weight: 800;
  box-shadow: 0px 32px 16px rgba(0, 0, 0, 0.09), 0px 16px 8px rgba(0, 0, 0, 0.09),
    0px 8px 4px rgba(0, 0, 0, 0.09), 0px 4px 2px rgba(0, 0, 0, 0.09),
    0px 2px 1px rgba(0, 0, 0, 0.09);
  border-radius: 10px;
  border: none;
  transition: background-color 250ms $bezier;

  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -337px;

  &:hover,
  &:focus {
    background-color: #089b6a;
  }
}

@media screen and (min-width: 768px) {
  .hero__firstImg-wrapper {
    background-image: none;
    height: 230px;
    margin-bottom: 0;
  }

  .hero__secondImg-wrapper {
    background-image: none;
    background: linear-gradient(180deg, rgba(2, 2, 19, 0) 0%, #020213 100%);
    height: 115px;
    background-position: bottom;
  }

  .hero__second-wrapper {
    height: 338px;
    max-width: 100%;
    background-image: url('../images/hero/hero-tablet.png');
    background-position: center;
    margin-bottom: 51px;
  }

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    .hero__second-wrapper {
      background-image: url('../images/hero/hero-tablet@2x.png');
      background-size: contain;
    }
  }

  .hero__title {
    font-size: 56px;
    margin-bottom: 24px;
  }

  .hero__first-wrapper {
    margin-bottom: 19px;
  }

  .hero__button-wrapper {
    text-align: left;
  }

  .hero-btn {
    bottom: -405px;
    left: 54px;
    transform: translateY(0);
  }
}

@media screen and (min-width: 1440px) {
  .hero__second-wrapper {
    background-image: none;
    height: 676px;
    margin-bottom: 0;
  }

  .hero__firstImg-wrapper {
    height: 444px;
    background-image: url('../images/hero/hero-desk1.png');
    background-position: center;
    margin-bottom: 20px;
    background-repeat: no-repeat;
    background-size: auto;
  }

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    .hero__firstImg-wrapper {
      background-image: url('../images/hero/hero-desk1@2x.png');
      background-size: cover;
    }
  }

  .hero__secondImg-wrapper {
    height: 212px;
    background-image: linear-gradient(180deg, rgba(2, 2, 19, 0) 0%, #020213 100%),
      url('../images/hero/hero-desk2.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto;
  }

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    .hero__secondImg-wrapper {
      background-image: linear-gradient(180deg, rgba(2, 2, 19, 0) 0%, #020213 100%),
        url('../images/hero/hero-desk2@2x.png');
      background-size: cover;
    }
  }

  .hero__button-wrapper {
    display: none;
  }

  .hero__section {
    padding-bottom: 194px;
  }

  .hero__section > .container {
    position: relative;
  }

  .hero__first-wrapper {
    display: flex;
    padding-top: 100px;
    padding-bottom: 100px;
  }

  .hero__title {
    margin: 0;
    width: 430px;
    margin-right: 50px;
  }

  .hero__text {
    width: 576px;
    font-size: 24px;
  }

  .hero-btn {
    margin: 0;
    padding: 0;
    width: 378px;
    height: 212px;
    margin: 0;
    opacity: 0.9;
    bottom: -463px;
    left: 240px;
  }
}
